

import React, { useEffect, useState } from "react";
import Slider from "react-draggable-slider"
import "./style.css";

export default function App() {
    const [load, setLoad] = useState(false)
    const [sliderSettings, setSliderSettings] = useState({})

    useEffect(() => {
        const getIdeas = async () => {
            fetch('https://ideas-api.deta.dev/')
                .then(res => res.json())
                .then(data => {
                    setSliderSettings({
                        data: data,
                        speed: 3000,
                        easing: "expo",
                        bgColor: "#070707",
                        showButton: false,
                    })
                    setLoad(true)
                })


        }
        getIdeas()
    }, [load])
    return (
        <div className='App'>
            {!load && <div>Loading...</div>}
            {load && <Slider sliderSettings={sliderSettings} />}
        </div>
    )
}